export async function getEventDetailsAction(eventId: string): Promise<Event | null> {
  try {
    const event = await getEventById(eventId);
    return event || null;
  } catch (error) {
    console.error("获取活动详情错误 (action):", error);
    return null;
  }
}

export async function getUserProfile(userId: string): Promise<{ user: User; joinedEvents: Event[] } | null> {
  const user = await getUserById(userId);
  if (!user) {
    console.warn(`用户个人资料未找到: ${userId}`);
    return null;
  }

  const allEvents = await getEvents();

  const joinedEvents = allEvents.filter(event =>
    (user.joinedEventIds || []).includes(event.id)
  );

  const cleanedJoinedEvents = joinedEvents.map(ev => {
    const fullEventData = allEvents.find(e => e.id === ev.id);
    if (!fullEventData) return null;

    return {
      ...fullEventData,
      participants: fullEventData.participants.map(p => ({
        id: p.id,
        username: p.username,
        avatarUrl: p.avatarUrl,
        isCheckedIn: p.isCheckedIn,
        op: p.op || 0, // Ensure op is carried over for participants display if needed
      })),
    };
  }).filter(Boolean) as Event[];

  return {
    user,
    joinedEvents: cleanedJoinedEvents
  };
}
